/* Print Export Styles */
.print-export-container {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.print-btn,
.export-btn,
.edit-btn,
.create-btn,
.add-activity-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-width: 120px;
  justify-content: center;
}

.print-btn {
  background: #3b82f6;
  color: white;
}

.print-btn:hover {
  background: #1e3a8a;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(59, 130, 246, 0.3);
}

.export-btn {
  background: #10b981;
  color: white;
}

.export-btn:hover {
  background: #059669;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(16, 185, 129, 0.3);
}

.edit-btn {
  background: #f59e0b;
  color: white;
}

.edit-btn:hover {
  background: #d97706;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(245, 158, 11, 0.3);
}

.edit-btn.edit-active {
  background: #dc2626;
}

.edit-btn.edit-active:hover {
  background: #b91c1c;
  box-shadow: 0 4px 8px rgba(220, 38, 38, 0.3);
}

.create-btn {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
  font-weight: 700;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.create-btn:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(16, 185, 129, 0.4);
}

.add-activity-btn {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  color: white;
  font-weight: 700;
  box-shadow: 0 4px 12px rgba(139, 92, 246, 0.3);
}

.add-activity-btn:hover {
  background: linear-gradient(135deg, #7c3aed 0%, #6d28d9 100%);
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(139, 92, 246, 0.4);
}

.print-btn:active,
.export-btn:active,
.edit-btn:active {
  transform: translateY(0);
}

/* Print Styles */
@media print {
  .print-export-container {
    display: none !important;
  }
  
  .no-print {
    display: none !important;
  }
  
  body {
    margin: 0 !important;
    padding: 0 !important;
  }
  
  .page {
    box-shadow: none !important;
    margin: 0 !important;
    padding: 20px !important;
  }
  
  .header {
    background: none !important;
    color: black !important;
    box-shadow: none !important;
  }
  
  .nav {
    display: none !important;
  }
  
  .footer {
    background: none !important;
    color: black !important;
  }
  
  .card,
  .program-card,
  .session-card,
  .report-card,
  .level-card {
    box-shadow: none !important;
    border: 1px solid #ccc !important;
    page-break-inside: avoid;
  }
  
  .add-activity-btn,
  .search-filter-container {
    display: none !important;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .print-export-container {
    flex-direction: column;
    gap: 0.75rem;
  }

  .print-btn,
  .export-btn,
  .edit-btn,
  .create-btn {
    width: 100%;
    padding: 0.875rem 1rem;
    font-size: 0.95rem;
  }

  .create-btn {
    order: -1; /* يظهر أولاً في الموبايل */
  }
}

@media (max-width: 480px) {
  .print-btn,
  .export-btn,
  .edit-btn,
  .create-btn {
    padding: 0.625rem 1rem;
    font-size: 0.9rem;
    min-width: 100px;
  }
}
